.social__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 24px;
  margin: 0;
  box-sizing: border-box;
  justify-content: center;
  padding: 20px 0;
  border-bottom: 1px solid $border-grey;

  @media (min-width: $tablet-width) {
    border: none;
    padding: 0;
    max-width: 132px;
  }
}

.social__icon {
  fill: $icon-basic;

  &:hover,
  &:focus-visible {
    fill: $bg-button-basic;
  }

  &:active {
    opacity: 0.3;
  }
}

.social__link {
  display: block;
  width: 28px;
  height: 22px;
}
