.map__wrapper {
  padding: 26px 20px;
  display: grid;
  grid-template-columns: repeat(2, 140px);
  align-items: center;
  box-sizing: border-box;
  width: $mobile-width;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    grid-template-columns: repeat(2, 163px);
    padding: 61px 70px;
    gap: 302px;
    width: $tablet-width;
  }

  @media (min-width: $desktop-width) {
    gap: 87px;
    width: 565px;
    background-color: $bg-basic-light;
    margin-left: 110px;
    position: relative;
    padding: 60px 78px 60px 80px;
    z-index: 1;
  }
}

.map__container {
  @media (min-width: $desktop-width) {
    min-height: 400px;
    position: relative;
    padding-top: 95px;
    padding-bottom: 105px;
    box-sizing: border-box;
  }
}

.map__container--invert {
  background-color: $bg-grey;
}

.map__link {
  background-color: $bg-grey;
  display: block;
  height: 362px;
  padding: 0;
  width: 100%;

  @media (min-width: $tablet-width) {
    height: 400px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
  }
}

.map__title {
  color: $text-dark-medium;
  font-family: $font-oswald;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  display: flex;
  flex-wrap: wrap;

  @media (min-width: $tablet-width) {
    color: $text-dark-medium;
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
  }
}

.map__descr {
  color: $text-grey400;
  font-family: $font-lato;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
  font-style: normal;
  display: flex;
  flex-wrap: wrap;

  @media (min-width: $tablet-width) {
    color: $text-grey222;
    text-align: right;
    font-size: 16px;
    font-weight: 400;
    min-height: 80px;
    justify-content: flex-end;
    gap: 20px;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
    justify-content: flex-start;
  }
}
