@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswalregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.page__body {
  margin: 0;
}

.visibility-none {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}

.button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 8px 44px;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  border: none;
  color: #ffffff;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  cursor: pointer;
}

.button--primary {
  padding: 10px 5px;
  background-color: #68b738;
}
.button--primary:hover, .button--primary:focus-visible {
  background-color: #5eaa2f;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
  padding: 10px 0;
}
.button--secondary:hover, .button--secondary:focus-visible, .button--secondary:active {
  background-color: #ebebeb;
}

