    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  width: 320px;
  margin: 0 auto;
  padding: 0 0 2px 0;
  text-align: center;
  z-index: 20;
  background-color: #ffffff;
  position: absolute;
}
@media (min-width: 768px) {
  .main-nav__list {
    width: 436px;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 33px;
    flex-wrap: wrap;
    position: relative;
    padding: 2px 0 2px 0;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    background-color: transparent;
    width: 478px;
  }
}

.main-nav__toggle {
  cursor: pointer;
  border: none;
  display: block;
  width: 24px;
  height: 16px;
  padding: 4px 0;
  box-sizing: border-box;
  background-color: #ffffff;
  position: absolute;
  top: 22px;
  right: 20px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    display: block;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(-45deg);
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    display: block;
    position: static;
    min-height: 0;
  }
}

