.main-nav__list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  width: 320px;
  margin: 0 auto;
  padding: 0 0 2px 0;
  text-align: center;
  z-index: 20;
  background-color: $bg-basic-light;
  position: absolute;

  @media (min-width: $tablet-width) {
    width: 436px;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 33px;
    flex-wrap: wrap;
    position: relative;
    padding: 2px 0 2px 0;
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
    width: 478px;
  }
}

.main-nav__toggle {
  cursor: pointer;
  border: none;
  display: block;
  width: 24px;
  height: 16px;
  padding: 4px 0;
  box-sizing: border-box;
  background-color: $bg-basic-light;
  position: absolute;
  top: 22px;
  right: 20px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: $text-basic-dark;
  box-shadow: 0 8px 0 0 $text-basic-dark, 0 16px 0 0 $text-basic-dark;
}

.main-nav--opened .main-nav__list {
  @media (max-width: 767px) {
    display: block;
  }
}

.main-nav--closed .main-nav__list {
  @media (max-width: 767px) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: $text-basic-dark;
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: $text-basic-dark;
    transform: rotate(-45deg);
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__list {
  @media (max-width: 767px) {
    display: block;
    position: static;
    min-height: 0;
  }
}
